<template>
  <main class="container">
    <language-picker
      v-model="language"
      :languages="languages"
    ></language-picker>

    <word-info
      :wordCount="wordCount"
      :translatedWordCount="translatedWordCount"
    ></word-info>

    <word-category
      v-for="category in categories"
      v-bind:key="category.name"
      :name="category.name"
      :words="category.words"
      :language="language"
    ></word-category>
  </main>
</template>

<script>
import languageData from '@/data/languages.json';
import wordsData from '@/data/words.json';
import LanguagePicker from '@/components/LanguagePicker';
import WordInfo from '@/components/WordInfo';
import WordCategory from '@/components/WordCategory';

export default {
  name: 'app-main',
  components: {
    LanguagePicker,
    WordInfo,
    WordCategory,
  },
  data() {
    return {
      language: '',
      wordCount: 0,
    };
  },
  computed: {
    languages() {
      return Object.entries(languageData).map((language) => {
        const [slug, { name, nativeName }] = language;
        return {
          slug,
          name,
          nativeName,
        };
      });
    },
    categories() {
      const all = [];
      wordsData.forEach((word) => {
        this.wordCount += 1;
        const wordCategory = word.category;
        const category = all.find(({ name }) => name === wordCategory);
        if (!category) {
          all.push({
            name: wordCategory,
            words: [word],
          });
        } else {
          category.words.push(word);
        }
      });
      return all;
    },
    translatedWordCount() {
      const choosedLanguage = this.language;
      /* eslint-disable no-param-reassign */
      return wordsData.reduce((total, { translations }) => {
        if (translations[choosedLanguage]) {
          /* eslint-disable no-return-assign */
          return total += 1;
        }
        return total;
      }, 0);
    },
  },
};
</script>

<style>
.container {
  width: 80%;
}
</style>



