<template>
  <div class="column">
    <b-tooltip
      :label="translation"
      animated
      type="is-light"
    >
      <a>{{emoji}}</a>
    </b-tooltip>
  </div>
</template>

<script>
export default {
  name: 'word-item',
  props: [
    'name',
    'emoji',
    'translation',
  ],
};
</script>

<style scoped>
div {
  font-size: 2.5rem;
}
div:hover {
  position: relative;
  bottom: 2px;
}
</style>


